.list{
    color:aliceblue;
    width: 360px;
    background-color:#bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;

}



.list p{
    padding: 10px;
    position: relative;
}

.delete{
    position:absolute;
    background-color: black;
    color:aliceblue;
    right:10px;
}

.list p input{
    background-color: #bf07ac;
    color: aliceblue;
    border:0px;
    font-size: 18px;
}
.list p input:focus{
    outline:none;
}




